.photos-container{
    position: relative;
    z-index:1;
}
.photo-list{
   line-height: 0;       
    -webkit-column-count: 5;    
    -webkit-column-gap: 0px;    
    -moz-column-count: 5;
    -moz-column-gap: 0px;
     column-count: 5;    
     column-gap: 0px;    
       display:inline-block;    
   margin-right: auto;    
   margin-left: auto;  
}

 
.photo-list img {    
   width: 100% !important;    
   height: auto !important;  
}  
